import EnvironmentError from "../errors/clienterrors/EnvironmentError";

export type AppEnvironment = "development" | "production" | "test";

enum DevSentryEnabled {
  FALSE = "FALSE",
  TRUE = "TRUE",
}

const getAppName = (): string => {
  const appName: string | undefined = process.env.REACT_APP_NAME;
  if (!appName) {
    throw new EnvironmentError(
      "REACT_APP_NAME not found",
      "React app name environment value is not defined."
    );
  }
  return appName;
};

const getVersion = (): string => {
  const version: string | undefined = process.env.REACT_APP_VERSION;
  if (!version) {
    throw new EnvironmentError(
      "REACT_APP_VERSION not found",
      "React app version environment value is not defined."
    );
  }
  return version;
};

const getEnvironment = (): AppEnvironment => {
  return process.env.NODE_ENV;
};

const getReleaseName = (): string => {
  const appName: string | undefined = process.env.REACT_APP_NAME;
  const version: string | undefined = process.env.REACT_APP_VERSION;
  if (appName && version) {
    return `${appName}@${version}`;
  }
  throw new EnvironmentError(
    "Release name error",
    "REACT_APP_NAME and REACT_APP_VERSION environment variables are not defined. These variables required to get release name."
  );
};

const getFirebaseConfig = (): Object => {
  const apiKey: string | undefined = process.env.REACT_APP_FIREBASE_API_KEY;
  const authDomain: string | undefined =
    process.env.REACT_APP_FIREBASE_AUTH_DOMAIN;
  const projectId: string | undefined =
    process.env.REACT_APP_FIREBASE_PROJECT_ID;
  const storageBucket: string | undefined =
    process.env.REACT_APP_FIREBASE_STORAGE_BUCKET;
  const messagingSenderId: string | undefined =
    process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID;
  const appId: string | undefined = process.env.REACT_APP_FIREBASE_APP_ID;
  const measurementId: string | undefined =
    process.env.REACT_APP_MEASUREMENT_ID;

  if (
    apiKey &&
    authDomain &&
    projectId &&
    storageBucket &&
    messagingSenderId &&
    appId &&
    measurementId
  ) {
    return {
      apiKey,
      authDomain,
      projectId,
      storageBucket,
      messagingSenderId,
      appId,
      measurementId,
    };
  }

  throw new EnvironmentError(
    "Firebase config error",
    "Firebase configs environment variables are not defined."
  );
};

const isDevSentryEnabled = (): boolean => {
  const devSentryEnabled: string | undefined =
    process.env.REACT_APP_DEV_SENTRY_ENABLED;
  return devSentryEnabled === DevSentryEnabled.TRUE;
};

export {
  getAppName,
  getVersion,
  getEnvironment,
  getReleaseName,
  getFirebaseConfig,
  isDevSentryEnabled,
};
