export enum Environment {
  DEVELOPMENT = "development",
  TEST = "test",
  PRODUCTION = "production",
}

export enum Status {
  SUCCESS = "SUCCESS",
  FAILED = "FAILED",
}

export type Response<Data, Error> = {
  status: Status;
  data?: Data;
  error?: Error;
};

export type ProgressResponse<Data, Error> = {
  status: Status;
  progress: number; // 0 to 1 progress [0.00 to 1.00]
  data?: Data;
  error?: Error;
};

export type RemoteConfigSentryOptions = {
  browserOptions: {
    allowUrls: Array<string | RegExp>;
    attachStacktrace: boolean;
    autoSessionTracking: boolean;
    debug: boolean;
    denyUrls: Array<string | RegExp>;
    dsn: string;
    enabled: boolean;
    logLevel: 0 | 1 | 2 | 3;
    maxBreadcrumbs: number;
    sampleRate: number;
    tracesSampleRate: number;
  };
};

export type Logger = {
  info: (message: string) => void;
  warn: (message: string) => void;
  error: (message: string) => void;
  debug: (message: string) => void;
};

export enum Network {
  ONLINE = "online",
  OFFLINE = "offline",
}

export enum PermissionState {
  GRANTED = "granted",
  PROMPT = "prompt",
  DENIED = "denied",
  ERROR = "error",
}
