import { CRASHLYTICS_ERROR } from "../ErrorCode";
import AppError, { AppErrorMessage } from "../AppError";

const crashLyticsErrorMessages: { [messageKey: string]: AppErrorMessage } = {
  firebaseRemoteConfigPropsUndefined: {
    message: "Props firebaseRemoteConfig undefined",
    description:
      "Props firebaseRemoteConfig is undefined due to firebase app initialization error.",
  },
  sentryRemoteConfigEmptyString: {
    message: "Sentry remote config not found",
    description: "Sentry remote config is undefined or empty string.",
  },
};

export { crashLyticsErrorMessages };

class CrashlyticsError extends AppError {
  constructor(message: string, description: string) {
    super(message, description, CRASHLYTICS_ERROR);
  }
}

export default CrashlyticsError;
