import React from "react";
import { createStyles, Theme, withStyles, WithStyles } from "@material-ui/core";
import * as THREE from "three";
import * as dat from "dat.gui";
import fontJson from "./sample.json";
import { Permission } from "../../utils/providers/Permissions/PermissionProvider";

const styles = (theme: Theme) =>
  createStyles({
    root: {
      overflow: "hidden",
      position: "fixed",
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
    },
  });

type Props = {
  online: boolean;
  permissions: Permission[];
  error?: any;
  isOnline: () => boolean;
};

type State = {};

class PermissionError extends React.Component<
  Props & WithStyles<typeof styles>,
  State
> {
  private permissionErrorRef = React.createRef<HTMLDivElement>();
  private gui: dat.GUI;
  private scene: THREE.Scene;
  private camera: THREE.PerspectiveCamera;
  private width: number;
  private height: number;
  private renderer: THREE.WebGLRenderer;
  private frameId?: number;
  private cube?: THREE.Mesh;
  private textMessage?: THREE.Mesh;
  private cubeFolder?: dat.GUI;
  private cameraFolder?: dat.GUI;
  constructor(props: Props & WithStyles<typeof styles>) {
    super(props);
    this.state = {};
    this.gui = new dat.GUI();
    this.scene = new THREE.Scene();
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.camera = new THREE.PerspectiveCamera(
      100,
      this.width / this.height,
      0.1,
      1000
    );
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.gui.hide();
  }

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    this.gui.show();
    if (this.permissionErrorRef.current) {
      this.init();
    }
  }

  componentWillUnmount() {
    this.stop();
    this.destroyContext();
  }

  init = () => {
    this.camera.position.z = 10;
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    this.scene.add(this.camera);
    this._cameraGUIHelper();
    this.loadText();
    this.loadCube();
    this.renderer.setSize(this.width, this.height);
    this.permissionErrorRef.current?.appendChild(this.renderer.domElement);
    this.start();
  };

  handleResize = () => {
    this.width = window.innerWidth;
    this.height = window.innerHeight;
    this.renderer.setSize(this.width, this.height);
    this.camera.aspect = this.width / this.height;
    this.camera.updateProjectionMatrix();
  };

  stop = () => {
    if (this.frameId !== undefined) {
      cancelAnimationFrame(this.frameId);
    }
  };

  destroyContext = () => {
    if (this.permissionErrorRef.current) {
      this.permissionErrorRef?.current?.removeChild(this.renderer.domElement);
      this.renderer.forceContextLoss();
    }
  };

  loadText = () => {
    const loader = new THREE.FontLoader();
    const font = loader.parse(fontJson);
    const textGeometry = new THREE.TextGeometry("Ashish Chaudhary", {
      font: font,
      size: 80,
      height: 10,
      bevelThickness: 2,
    });
    const materialText = new THREE.MeshBasicMaterial({
      color: 0xffff00,
    });
    const textMessage = new THREE.Mesh(textGeometry, materialText);
    textMessage.position.z = -850;
    textMessage.geometry.center();
    textMessage.position.y = 150;
    this.textMessage = textMessage;
    this.scene.add(textMessage);
  };

  loadCube = () => {
    const geometry = new THREE.BoxGeometry();
    const materialCube = new THREE.MeshBasicMaterial({ color: 0xf8c471 });
    const cube = new THREE.Mesh(geometry, materialCube);
    this._cubeGUIHelper(cube);
    this.cube = cube;
    this.scene.add(cube);
  };

  _cameraGUIHelper = () => {
    const cameraFolder = this.gui.addFolder("Camera");
    if (this.cameraFolder) {
      this.cameraFolder.destroy();
    }
    cameraFolder.add(this.camera.position, "x", -10, 10, 0.01);
    cameraFolder.add(this.camera.position, "y", -10, 10, 0.01);
    cameraFolder.add(this.camera.position, "z", -10, 10, 0.01);
    this.cameraFolder = cameraFolder;
    cameraFolder.open();
  };

  _cubeGUIHelper = (cube: THREE.Mesh) => {
    const cubeFolder = this.gui.addFolder("Cube");
    if (this.cubeFolder) {
      this.cubeFolder.destroy();
    }
    cubeFolder.add(cube.rotation, "x", 0, Math.PI * 2, 0.01);
    cubeFolder.add(cube.rotation, "y", 0, Math.PI * 2, 0.01);
    cubeFolder.add(cube.rotation, "z", 0, Math.PI * 2, 0.01);
    this.cubeFolder = cubeFolder;
    cubeFolder.open();
  };

  start = () => {
    if (this.frameId === undefined || this.frameId == null) {
      this.frameId = requestAnimationFrame(this.animate);
    }
  };

  animate = () => {
    this.frameId = requestAnimationFrame(this.animate);
    this.update();
    this.renderScene();
  };

  renderScene = () => {
    this.renderer.render(this.scene, this.camera);
  };

  update = () => {
    if (this.textMessage) {
      this.textMessage.rotation.y += 0.01;
    }
    if (this.cube) {
      this.cube.rotation.x += 0.01;
      this.cube.rotation.y += 0.01;
    }
  };

  render() {
    const { classes } = this.props;
    return <div ref={this.permissionErrorRef} className={classes.root} />;
  }
}

export default withStyles(styles)(PermissionError);
