import React, { ReactNode } from "react";
import PermissionError from "../../../components/PermissionError";
import { cdb } from "../../../db/ClientDB";
import { Network, PermissionState } from "../../types";
import { permissions as appPermissions } from "./permissions";

export type Permission = {
  key: string;
  name: string;
  required: boolean;
  runtime: boolean;
  info: string;
  isAllowed: boolean;
};

export type PermissionContext = {
  online: boolean;
  permissions: Permission[];
  requestForPermission?: (key: string) => Promise<PermissionState>;
  isOnline?: () => boolean;
};

const defaultContext: PermissionContext = {
  online: true,
  permissions: [],
};

const Context = React.createContext<PermissionContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type Props = {
  children: ReactNode;
};

type State = {
  resolved: boolean;
  error?: any;
  permissions: Permission[];
  online: boolean;
};

class PermissionProvider extends React.Component<Props, State> {
  readonly state: State = {
    resolved: false,
    error: null,
    online: true,
    permissions: [],
  };
  handleNetworkEvent =
    (eventName: Network.ONLINE | Network.OFFLINE) => (e: any) => {
      this.setState({
        online: eventName === Network.ONLINE,
      });
    };

  isOnline = (): boolean => {
    try {
      return navigator && navigator.onLine;
    } catch (error) {
      return false;
    }
  };

  isCookieEnabled = (): boolean => {
    try {
      return navigator && navigator.cookieEnabled;
    } catch (error) {
      return false;
    }
  };

  isDBOpen = async (): Promise<boolean> => {
    try {
      await cdb.open();
      return true;
    } catch (error) {
      return false;
    }
  };

  isAppStorageAllowed = async (): Promise<boolean> => {
    try {
      let localAnsSessionStorage = false;
      if (window.localStorage && window.sessionStorage) {
        localAnsSessionStorage = true;
      }
      const database = await this.isDBOpen();
      return localAnsSessionStorage && database;
    } catch (error) {
      return false;
    }
  };

  requestForPermission = async (
    permissionName: string
  ): Promise<PermissionState> => {
    try {
      const requestPermission = this.state.permissions.filter(
        (data: Permission) =>
          data.key === permissionName &&
          ["camera", "microphone", "notification", "geolocation"].includes(
            permissionName
          )
      );
      if (requestPermission.length === 1) {
        const permissionState = await navigator.permissions.query({
          name: permissionName as PermissionName,
        });
        if (permissionState.state === PermissionState.GRANTED) {
          return PermissionState.GRANTED;
        } else if (permissionState.state === PermissionState.PROMPT) {
          return PermissionState.PROMPT;
        } else if (permissionState.state === PermissionState.DENIED) {
          return PermissionState.DENIED;
        } else {
          throw new Error(
            `Permission of ${requestPermission[0].name} is required!`
          );
        }
      } else {
        throw new Error(
          `Permission function didn't configured for ${permissionName}`
        );
      }
    } catch (error) {
      return PermissionState.ERROR;
    }
  };

  componentDidMount() {
    window.addEventListener("online", this.handleNetworkEvent(Network.ONLINE));
    window.addEventListener(
      "offline",
      this.handleNetworkEvent(Network.OFFLINE)
    );
    Promise.allSettled([
      this.isOnline(),
      this.isCookieEnabled(),
      this.isAppStorageAllowed(),
      this.requestForPermission("camera"),
      this.requestForPermission("microphone"),
      this.requestForPermission("notification"),
      this.requestForPermission("geolocation"),
    ])
      .then((permissionResponse) => {
        const isOnline: boolean =
          permissionResponse[0].status === "fulfilled" &&
          permissionResponse[0].value;
        const permissions = appPermissions.map((permissionObj: Permission) => {
          if (permissionObj.key === "cookie") {
            permissionObj.isAllowed =
              permissionResponse[1].status === "fulfilled"
                ? permissionResponse[1].value
                : false;
          }
          if (permissionObj.key === "app-storage") {
            permissionObj.isAllowed =
              permissionResponse[2].status === "fulfilled" &&
              permissionResponse[2].value;
          }
          if (permissionObj.key === "location") {
            permissionObj.isAllowed =
              permissionResponse[3].status === "fulfilled" &&
              permissionResponse[3].value === PermissionState.GRANTED;
          }
          if (permissionObj.key === "camera") {
            permissionObj.isAllowed =
              permissionResponse[4].status === "fulfilled" &&
              permissionResponse[4].value === PermissionState.GRANTED;
          }
          if (permissionObj.key === "microphone") {
            permissionObj.isAllowed =
              permissionResponse[5].status === "fulfilled" &&
              permissionResponse[5].value === PermissionState.GRANTED;
          }
          if (permissionObj.key === "notification") {
            permissionObj.isAllowed =
              permissionResponse[6].status === "fulfilled" &&
              permissionResponse[6].value === PermissionState.GRANTED;
          }
          return permissionObj;
        });
        const isResolved: boolean =
          permissions.filter(
            (data: Permission) => data.required && !data.isAllowed
          ).length === 0;

        this.setState({
          permissions,
          resolved: isResolved,
          online: isOnline,
        });
      })
      .catch((error: any) => {
        this.setState({
          resolved: false,
          error: error?.message ?? "Unknown Error!",
        });
      });
  }

  render() {
    const { resolved, permissions, online, error } = this.state;
    if (resolved && true) {
      return (
        <PermissionError
          online={online}
          permissions={permissions}
          error={error}
          isOnline={this.isOnline}
        />
      );
    }
    return (
      resolved &&
      false && (
        <Provider
          value={{
            permissions,
            online,
            isOnline: this.isOnline,
            requestForPermission: this.requestForPermission,
          }}
        >
          {this.props.children}
        </Provider>
      )
    );
  }
}

export default PermissionProvider;
