import Dexie from "dexie";
import { ClientConfig, IClientConfig } from "./client/models/ClientConfig";

export class ClientDB extends Dexie {
  clientConfig: Dexie.Table<IClientConfig, string>;
  constructor() {
    super("ClientDB");
    var cdb = this;
    cdb.version(1).stores({
      client_config: "&key",
    });
    this.clientConfig = this.table("client_config");
    cdb.clientConfig.mapToClass(ClientConfig);
  }
}

export var cdb = new ClientDB();
