export enum ErrorType {
  CLIENT_ERROR,
  SERVER_ERROR,
  UNKNOWN_ERROR,
}
export type AppErrorMessage = {
  message: string;
  description: string;
};

const fixProto = (target: Error, prototype: {}): void => {
  const setPrototypeOf: Function = (Object as any).setPrototypeOf;
  setPrototypeOf
    ? setPrototypeOf(target, prototype)
    : ((target as any).__proto__ = prototype);
};

const fixStack = (target: Error, fn: Function = target.constructor): void => {
  const captureStackTrace: Function = (Error as any).captureStackTrace;
  captureStackTrace && captureStackTrace(target, fn);
};

class AppError extends Error {
  public code: number;
  public type: ErrorType;
  public description: string;
  constructor(
    message: string,
    description: string,
    code?: number,
    type?: ErrorType
  ) {
    super(message);
    this.description = description;
    this.code = code ?? 1000;
    this.type = type ?? ErrorType.UNKNOWN_ERROR;

    Object.defineProperty(this, "name", {
      value: new.target.name,
      enumerable: false,
      configurable: true,
    });

    fixProto(this, new.target.prototype);
    fixStack(this);
  }
}

export default AppError;
