import React, { ReactNode } from "react";
import * as Sentry from "@sentry/react";
import { Integrations } from "@sentry/tracing";
import { BrowserOptions } from "@sentry/react";
import {
  getEnvironment,
  getReleaseName,
  isDevSentryEnabled,
} from "../../environment";
import { FirebaseConsumer } from "../Firebase";
import firebase from "firebase/app";
import {
  Environment,
  Logger,
  RemoteConfigSentryOptions,
  Response,
  Status,
} from "../../types";
import CrashlyticsError, {
  crashLyticsErrorMessages,
} from "../../../errors/clienterrors/CrashlyticsError";
import { SENTRY_CONFIG } from "../../constants";

export type CrashlyticsContext = {
  logger?: Logger;
};

const defaultContext: CrashlyticsContext = {};

const Context = React.createContext<CrashlyticsContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

type State = {
  error?: any;
  resolved: boolean;
};

type Props = {
  children: ReactNode;
};

type CrashlyticsProps = {
  children: ReactNode;
  firebaseRemoteConfig?: firebase.remoteConfig.RemoteConfig;
};

class Crashlytics extends React.Component<CrashlyticsProps, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      error: null,
      resolved: false,
    };
  }

  getLogger = (): Logger => {
    const info = (message: string) => {
      Sentry.captureMessage(message, Sentry.Severity.Info);
    };
    const error = (message: string): void => {
      Sentry.captureMessage(message, Sentry.Severity.Error);
    };
    const debug = (message: string): void => {
      Sentry.captureMessage(message, Sentry.Severity.Debug);
    };
    const warn = (message: string): void => {
      Sentry.captureMessage(message, Sentry.Severity.Warning);
    };
    return {
      info,
      warn,
      debug,
      error,
    };
  };

  getSentryBrowserOptions = async (): Promise<
    Response<BrowserOptions, any>
  > => {
    const environment = getEnvironment();
    const releaseName = getReleaseName();
    try {
      const { firebaseRemoteConfig } = this.props;
      const {
        firebaseRemoteConfigPropsUndefined,
        sentryRemoteConfigEmptyString,
      } = crashLyticsErrorMessages;
      if (!firebaseRemoteConfig) {
        throw new CrashlyticsError(
          firebaseRemoteConfigPropsUndefined.message,
          firebaseRemoteConfigPropsUndefined.description
        );
      }
      const sentryOptionValue: string =
        firebaseRemoteConfig.getString(SENTRY_CONFIG);
      if (
        !sentryOptionValue &&
        (environment === Environment.DEVELOPMENT ||
          environment === Environment.TEST)
      ) {
        throw new CrashlyticsError(
          sentryRemoteConfigEmptyString.message,
          sentryRemoteConfigEmptyString.description
        );
      } else {
        const { browserOptions }: RemoteConfigSentryOptions =
          JSON.parse(sentryOptionValue);
        return {
          status: Status.SUCCESS,
          data: {
            environment,
            release: releaseName,
            enabled:
              (environment === Environment.DEVELOPMENT &&
                !isDevSentryEnabled()) ||
              environment === Environment.TEST
                ? false
                : browserOptions.enabled,
            integrations: [new Integrations.BrowserTracing()],
            dsn: browserOptions.dsn,
            allowUrls: browserOptions.allowUrls,
            attachStacktrace: browserOptions.attachStacktrace,
            autoSessionTracking: browserOptions.autoSessionTracking,
            debug: browserOptions.debug,
            denyUrls: browserOptions.denyUrls,
            logLevel: browserOptions.logLevel,
            maxBreadcrumbs: browserOptions.maxBreadcrumbs,
            sampleRate: browserOptions.sampleRate,
            tracesSampleRate: browserOptions.tracesSampleRate,
          },
        };
      }
    } catch (error) {
      return {
        status: Status.FAILED,
        error,
      };
    }
  };
  componentDidMount() {
    this.getSentryBrowserOptions()
      .then((response) => {
        if (response.status === Status.SUCCESS) {
          Sentry.init(response.data!);
        } else {
          Sentry.init({
            enabled: false,
          });
        }
        this.setState({
          resolved: true,
        });
      })
      .catch((error: any) => {
        Sentry.init({
          enabled: false,
        });
        this.setState({
          resolved: true,
          error,
        });
      });
  }
  render() {
    const { resolved } = this.state;
    const logger = this.getLogger();
    return (
      resolved && (
        <Provider
          value={{
            logger,
          }}
        >
          {this.props.children}
        </Provider>
      )
    );
  }
}

const CrashlyticsProvider: React.FC<Props> = ({ children }) => {
  return (
    <FirebaseConsumer>
      {({ firebaseRemoteConfig }) => {
        return (
          <Crashlytics
            children={children}
            firebaseRemoteConfig={firebaseRemoteConfig}
          />
        );
      }}
    </FirebaseConsumer>
  );
};

export default CrashlyticsProvider;
