import React from "react";
import { CrashlyticsConsumer } from "./utils/providers/Crashlytics";
import { Logger } from "./utils/types";

type Props = {
  logger?: Logger;
};
type State = {};

class TempTest extends React.Component<Props, State> {
  render() {
    const { logger } = this.props;
    return (
      <div
        onClick={() => {
          logger?.debug("Hello");
        }}
      >
        Temp
      </div>
    );
  }
}
const Temp: React.FC = () => {
  return (
    <CrashlyticsConsumer>
      {({ logger }) => {
        return <TempTest logger={logger} />;
      }}
    </CrashlyticsConsumer>
  );
};

export default Temp;
