import React, { ErrorInfo, ReactNode } from "react";
import firebase from "firebase/app";
import "firebase/analytics";
import "firebase/remote-config";
import "firebase/database";
import "firebase/storage";
import "firebase/auth";

import {
  getEnvironment,
  getFirebaseConfig,
  getReleaseName,
} from "../../environment";

import { Environment, Response, Status } from "../../types";

export type FirebaseContext = {
  resolved: boolean;
  error?: any;
  firebaseApp?: firebase.app.App;
  firebaseAnalytics?: firebase.analytics.Analytics;
  firebaseRemoteConfig?: firebase.remoteConfig.RemoteConfig;
  firebaseDatabase?: firebase.database.Database;
  firebaseStorage?: firebase.storage.Storage;
  firebaseAuth?: firebase.auth.Auth;
};

const defaultContext: FirebaseContext = {
  resolved: false,
};

const Context = React.createContext<FirebaseContext>(defaultContext);

const { Consumer, Provider } = Context;

export { Consumer, Context };

export type FirebasePackages = {
  firebaseApp?: firebase.app.App;
  firebaseAnalytics?: firebase.analytics.Analytics;
  firebaseRemoteConfig?: firebase.remoteConfig.RemoteConfig;
  firebaseDatabase?: firebase.database.Database;
  firebaseStorage?: firebase.storage.Storage;
  firebaseAuth?: firebase.auth.Auth;
};

export type InitRemoteConfigResponse = {
  enable: boolean;
  firebaseRemoteConfig?: firebase.remoteConfig.RemoteConfig;
};

type State = {
  error?: any;
  resolved: boolean;
  firebaseApp?: firebase.app.App;
  firebaseAnalytics?: firebase.analytics.Analytics;
  firebaseRemoteConfig?: firebase.remoteConfig.RemoteConfig;
  firebaseDatabase?: firebase.database.Database;
  firebaseStorage?: firebase.storage.Storage;
  firebaseAuth?: firebase.auth.Auth;
};

type Props = {
  children: ReactNode;
};

class FirebaseProvider extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      error: null,
      resolved: false,
    };
  }

  initRemoteConfig = async (
    firebaseApp: firebase.app.App
  ): Promise<InitRemoteConfigResponse> => {
    try {
      const environment = getEnvironment();
      const firebaseRemoteConfig = firebaseApp.remoteConfig();
      if (environment === Environment.DEVELOPMENT) {
        firebaseRemoteConfig.settings.fetchTimeoutMillis = 5000;
        firebaseRemoteConfig.settings.minimumFetchIntervalMillis = 3600000;
      }
      await firebaseRemoteConfig.ensureInitialized();
      const isActivated = await firebaseRemoteConfig.fetchAndActivate();
      if (isActivated) {
        await firebaseRemoteConfig.ensureInitialized();
      }
      return {
        enable: true,
        firebaseRemoteConfig,
      };
    } catch (error) {
      return {
        enable: false,
      };
    }
  };

  init = async (): Promise<Response<FirebasePackages, any>> => {
    try {
      const firebaseConfig = getFirebaseConfig();
      const releaseName = getReleaseName();
      const firebaseApp = firebase.initializeApp(firebaseConfig, releaseName);
      const firebaseAnalytics = firebaseApp.analytics();
      const firebaseDatabase = firebaseApp.database();
      const firebaseStorage = firebaseApp.storage();
      const firebaseAuth = firebaseApp.auth();
      const firebaseRemoteConfigResponse = await this.initRemoteConfig(
        firebaseApp
      );
      return {
        status: Status.SUCCESS,
        data: {
          firebaseApp,
          firebaseAnalytics,
          firebaseDatabase,
          firebaseStorage,
          firebaseAuth,
          firebaseRemoteConfig: firebaseRemoteConfigResponse.enable
            ? firebaseRemoteConfigResponse.firebaseRemoteConfig
            : undefined,
        },
      };
    } catch (error: any) {
      return {
        status: Status.FAILED,
        error,
      };
    }
  };
  componentDidMount() {
    this.init().then((response: Response<FirebasePackages, any>) => {
      if (response.status === Status.SUCCESS) {
        this.setState({
          resolved: true,
          ...response.data,
        });
      } else {
        this.setState({
          resolved: true,
          error: response.error,
        });
      }
    });
  }

  componentDidCatch(error: Error, errorInfo: ErrorInfo) {}

  render() {
    const {
      error,
      resolved,
      firebaseApp,
      firebaseAnalytics,
      firebaseRemoteConfig,
      firebaseDatabase,
      firebaseStorage,
      firebaseAuth,
    } = this.state;
    if (error && resolved) {
      return <div>error</div>;
    }
    return (
      resolved && (
        <Provider
          value={{
            resolved,
            error,
            firebaseApp,
            firebaseAnalytics,
            firebaseRemoteConfig,
            firebaseDatabase,
            firebaseStorage,
            firebaseAuth,
          }}
        >
          {this.props.children}
        </Provider>
      )
    );
  }
}

export default FirebaseProvider;
